
#search-container{position:absolute;top:10%;left:0;bottom:0;right:0;margin:auto;width:90%;height:90%
}
#search-container .search{width:100%;height:100%;text-align:center;text-transform:uppercase
}
#search-container .search .search-input{padding:1rem 1.6rem;width:80%;font-size:1.2rem;color:#000;border:3px solid white;border-radius:5px;line-height:1em
}
#search-container .search .info{color:#fff;letter-spacing:.05em;opacity:.8
}


